<template>
  <div class='frame'>
    <router-view id='view'></router-view>

  </div>
</template>

<script>
export default {
  name: 'frame-content',
  methods: {
    routerChange (targetRouter) {
      this.$router.push({
        name: targetRouter
      })
      this.$bus.$emit('routerIconChangeLeft', targetRouter);
      this.$bus.$emit('routerIconChangeFooter', targetRouter);
    },
    notfound () {
      this.$router.push({
        name: '404',
      })
    },
  },
  mounted () {
    this.$bus.$on('routerChange', this.routerChange);

    if (document.location.href.includes('user')) {
      if (document.location.href.includes('id')) {
        this.$router.push({
          name: 'userAnother',
          params: {
            id: document.location.href.substring(document.location.href.indexOf('=') + 1)
          }
        })
      }
      else {
        this.$router.push({
          name: 'userMe'
        })
      }
    }
    else {
      let firstRouterList = ['hollow', 'market', 'news', 'laf'];
      for (let item of firstRouterList) {
        if (document.location.href.includes(item)) {
          if (document.location.href.includes('detail')) {
            this.$router.push({
              name: item + 'Detail',
              params: {
                id: document.location.href.substring(document.location.href.indexOf('=') + 1)
              }
            })
          }
          else {
            this.$router.push({
              name: item + 'List'
            })
          }
          break;
        }
      }
    }
  },
  beforeDestroy () {
    this.$bus.$off('routerChange');
  }
}
</script>

<style scoped>
.frame {
  padding-top: 50px;
  height: 100%;
  overflow-y: auto;
}
.floatButton {
  position: fixed;
  right: 40px;
  font-size: 25px;
  cursor: pointer;
}
.floatButtonTop {
  bottom: 200px;
}
</style>